.todo-history .todo-history-tabs .todo-history-tab-pane .daily-todos {
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 8px;
  display: flex;
}
.todo-history .todo-history-tabs .todo-history-tab-pane .daily-todos .title {
  flex-shrink: 0;
  margin-right: 8px;
}
.todo-history .todo-history-tabs .todo-history-tab-pane .daily-todos .title .date .date-time {
  margin-right: 4px;
  color: #222;
}
.todo-history .todo-history-tabs .todo-history-tab-pane .daily-todos .title .date .week-time {
  color: #888;
  font-size: 12px;
}
.todo-history .todo-history-tabs .todo-history-tab-pane .daily-todos .title .finished-count {
  font-size: 14px;
  color: #888;
}
.todo-history .todo-history-tabs .todo-history-tab-pane .daily-todos .details {
  flex: 1 1;
}
