.todo-history-item {
  padding: 8px;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
}
.todo-history-item .text .time {
  color: #999;
  margin-right: 8px;
}
.todo-history-item .text .description {
  color: #222;
}
.todo-history-item .action {
  display: none;
}
.todo-history-item .action span {
  color: #1890ff;
}
.todo-history-item .action span:first-child {
  margin-right: 4px;
}
.todo-history-item:hover {
  background: #f4f4f4;
}
.todo-history-item:hover .action {
  display: block;
  cursor: pointer;
}
