.statistics ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
.statistics ul li {
  border: 1px solid #ddd;
  overflow: hidden;
  cursor: pointer;
  position: relative;
}
.statistics ul li:first-child {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.statistics ul li:last-child {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
.statistics ul li + li {
  border-left: none;
}
.statistics ul li.active {
  background: #f4f4f4;
}
.statistics ul li .text {
  position: absolute;
  margin: 8px;
}
.statistics ul li .text p:first-child {
  font-size: 16px;
}
.statistics ul li .text p:nth-child(2) {
  font-size: 12px;
  color: #999;
}
.statistics ul li .text p:last-child {
  font-size: 32px;
}
.statistics ul li .graph {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}
.statistics ul li .graph svg {
  height: 60px;
  width: 239px;
  margin-left: -1px;
}
.statistics .detail-statistics {
  margin: 16px;
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
}
