.count-down {
  position: relative;
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 4px;
  overflow: hidden;
}
.count-down span {
  position: relative;
  z-index: 1;
}
.count-down .progress-bar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  background: #e9e9e9;
  z-index: 0;
}
