.add-model-item {
  margin: 0 16px;
  display: flex;
  align-items: center;
}
.add-model-item + .add-model-item {
  margin-top: 8px;
}
.add-model-item input {
  flex: 1 1;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane {
  position: relative;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane button {
  position: absolute;
  right: 0;
  top: -52px;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes {
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 8px;
  display: flex;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title {
  flex-shrink: 0;
  margin-right: 8px;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title .date .date-time {
  margin-right: 4px;
  color: #222;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title .date .week-time {
  color: #888;
  font-size: 12px;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .title .finished-count {
  font-size: 14px;
  color: #888;
}
.tomato-history .tomato-history-tabs .tomato-history-tab-pane .daily-tomatoes .details {
  flex: 1 1;
}
